
header {
	position: relative; }

nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 1; }

.hamburger {
	display: none; }

.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style: none;

	.menu_link {
		text-decoration: none;
		color: #ffffff;
		font-weight: 300;
		font-size: 15px; }

	.menu_item {
		border-right: 2px dotted black;
		padding-right: 50px; } }


.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background-color: rgba(0, 0, 0, 0.6);

	.subheader_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }

	.subheader_official {
		margin-top: 6px;
		opacity: 0.5;
		color: #ffffff;
		font-size: 13px;
		font-weight: 400; }

	.subheader_call {
		display: block;
		margin-top: 19px;
		color: #ffffff;
		font-size: 12px;
		font-weight: 300;
		text-align: right; }

	.subheader_phone {
		display: block;
		margin-top: 8px;
		text-align: right;
		color: #ffffff;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		text-decoration: none;
		line-height: 12px; }

	.subheader_btn1 {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, $color 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 14px;
		font-weight: 400;
		text-align: center;
		text-transform: uppercase;
		@include btn_hover; } }


.promo {
	min-height: 800px;
	background: url('../img/main_bg.jpg') center (center / cover) no-repeat;
	padding: 150px 0 135px 0;

	.promo_header {
		color: #ffffff;
		font-size: 60px;
		font-weight: 700;
		text-transform: uppercase;
		line-height: 60px;
		margin: 0;
		text-align: center; }

	.promo_subheader {
		color: #ffffff;
		font-size: 36px;
		font-weight: 700;
		text-transform: uppercase;
		line-height: 36px;
		margin: 0;
		margin-top: 22px;
		text-align: center; }

	.promo_descr {
		margin-top: 23px;
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;
		text-align: center; }

	.promo_btn1 {
		display: block;
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, $color 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase;
		@include btn_hover; } }

.reasons {
	min-height: 900px;
	padding: 71px 0 65px 0;

	.reasons_head {
		text-align: center; }

	.reasons_btn {
		width: 112px;
		height: 24px;
		background-color: #000000;
		color: #ffffff;
		font-size: 11px;
		font-weight: 400;
		text-transform: uppercase;
		transition: 0.5s all;
		&:hover {
			background-color: #ffffff;
			color: black; } }

	.reasons_header {
		margin-top: 38px;
		color: #222222;
		font-family: Roboto;
		font-size: 38px;
		font-weight: 700;
		text-transform: uppercase; }

	.reasons_wrapper {
		margin-top: 40px;
		display: flex;
		justify-content: center;
		align-items: center; }

	.reasons_round {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 26px;
		width: 113px;
		height: 113px;
		background-color: $color;
		border-radius: 100%; }

	.reasons_descr {
		width: 350px; }

	.reasons_subheader {
		color: $color;
		font-size: 17px;
		font-weight: 400; }

	.reasons_text {
		color: #202020;
		font-size: 15px;
		font-weight: 300;
		line-height: 21px; } }

.mobile {
	min-height: 864px;
	padding: 48px 0 80px 0;
	background: url('../img/mobile_img/mobile_bg.jpg') center (center / cover) no-repeat;
	.row {
		margin-top: 32px; }
	.mobile_head {
		text-align: center;
		.mobile_btn {
			width: 112px;
			height: 24px;
			background-color: #000000;
			color: #ffffff;
			font-size: 11px;
			font-weight: 400;
			text-transform: uppercase; }
		.mobile_header {
			margin-top: 38px;
			color: #222222;
			font-size: 38px;
			font-weight: 700;
			text-transform: uppercase; }
		.mobile_subheader {
			margin-top: 19px;
			color: #222222;
			font-size: 17px;
			font-weight: 400; } }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 92px;
		&_1 {
			background: url('../img/mobile_img/order_img.jpg') center (center / cover) no-repeat; }
		&_2 {
			background: url('../img/mobile_img/innings_img.jpg') center (center / cover) no-repeat; }
		&_3 {
			background: url('../img/mobile_img/prices_img.jpg') center (center / cover) no-repeat; }
		&_4 {
			background: url('../img/mobile_img/payment_img.jpg') center (center / cover) no-repeat; }
		&_5 {
			background: url('../img/mobile_img/connection_img.jpg') center (center / cover) no-repeat; }
		&_6 {
			background: url('../img/mobile_img/price_img.jpg') center (center / cover) no-repeat; }
		&_subtitle {
			color: #ffffff;
			font-size: 24px;
			font-weight: 400;
			line-height: 31px;
			text-align: center;
			text-transform: uppercase; }
		&_plus {
			display: flex;
			justify-content: centet;
			align-items: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			width: 26px;
			height: 26px;
			background-color: #000000;
			border-radius: 100%;
			padding: 6px;
			cursor: pointer;
			&:before {
				content: '';
				display: block;
				width: 14px;
				height: 2px;
				background-color: $color;
				transition: 0.3s all; }
			&:after {
				content: '';
				position: absolute;
				left: 50%;
				transform: translateX(-50%);
				display: block;
				width: 2px;
				height: 14px;
				background-color: $color;
				transition: 0.3s all; }
			&:hover {
				&:before, &:after {
					background-color: white; } } } } }
.car {
