.require {
	padding: 68px 0 84px 0;
	&_head {
		text-align: center; }
	&_btn {
		width: 112px;
		height: 24px;
		background-color: #000000;
		color: #ffffff;
		font-size: 11px;
		font-weight: 400; }
	&_header {
		margin-top: 33px;
		color: #222222;
		font-size: 38px;
		font-weight: 700;
		text-transform: uppercase; }
	&_block {
		margin: 33px 0; }
	&_nmb {
		margin-bottom: 0px; }
	&_title {
		color: #1ca8c3;
		font-size: 22px;
