				width: 21px;
				height: 20px;
				background: url('../icons/blue_mark.svg') center (center / cover) no-repeat; } } }
	&_descr {
		color: #1ca8c3;
		font-weight: 300;
		font-size: 17px;
		line-height: 24px;
		margin-top: 25px; }
	.warning {
		.require_title {
			color: #ff5656; }
