@media (max-width: 1200px) {
    .menu {
        .menu_item , .menu_item1 {
            padding-right: 20px; }
        .menu_link {
            font-size: 13px; } }
    .promo {
        .promo_header {
            font-size: 50px; } }
    .reasons {
        .reasons_wrapper {
            align-items: flex-start; }
        .reasons_text {
            width: 300px; } } }

@media (max-width: 991px) {
    .menu {
        .menu_item , .menu_item1 {
            padding-right: 8px; }
        .menu_link {
            font-size: 10px; } }
    .subheader {
        .subheader_logo {
            img {
                width: 80%; } }
        .subheader_official {
            font-size: 11px; }
        .subheader_phone {
            font-size: 18px; }
        .subheader_btn1 {
            width: 152px;
            height: 37px;
            margin-top: 23px; } }
    .promo {
        .promo_header {
            font-size: 40px; }
        .promo_subheader {
            font-size: 30px; } }
    .reasons {
        .reasons_wrapper {
            align-items: center;
            flex-direction: column; }
        .reasons_subheader {
            margin-top: 20px; } } }

@media (max-width: 767px) {
    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0,0,0, .8);
        transition: 0.6s all;
        &_active {
            left: 0; }
        .menu_item, .menu_item1 {
            padding-right: 0;
            border: none;
            margin: 10px 0; }
        .menu_link {
            font-size: 20px; } }
    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY()-50%;
        width: 25px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: #fff;
            margin-bottom: 2px;
            transition: 0.5s all; }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-child(1) {
                    transform: translateY(3px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                    transform: translateY(3px) rotate(45deg); } } } }
    .xs-hidden {
        display: none; }
    .xs-visible {
        display: block; } }
